@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@800&display=swap');

*{
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  -webkit-tap-highlight-color: transparent;
  -ms-overflow-style: none; /* for Internet Explorer, Edge */
  scrollbar-width: none; /* for Firefox */
  
  overflow-y: scroll; 

}
*::-webkit-scrollbar {
  display: none; /* for Chrome, Safari, and Opera */
}



  

  .topbar{
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
  

  }


  .left{
    display: flex;
    margin: 1%;
    color: white;
    
    .logo{
      color: white;
      text-decoration: none;
      cursor: pointer;
      :hover{
        color: white;
        text-decoration-color: white;
        animation: hoverer 1s linear infinite;

      }
    }
    
  }

  .right{
    display: flex;
    margin: 1%;
    color: white;
  }

  .aboutlink{
    display: flex;
    margin: 3px;
    color: white;
    text-decoration: none;
    cursor: pointer;
    
    :hover{
      color: white;
      text-decoration-color: white;
      animation: hoverer 1s linear infinite;
      
    }
    
  }
  
  .info{
    bottom: 1%;
    right: 0.5%;
    position: absolute;
    
    
    :hover{
      animation: hoverer 1s linear infinite;
    }
  }

  @keyframes hoverer {
    50% {
      opacity: 0.5;
    }
  }
  
  


 .app .vid .video{
    height: 100vh;
    width: 100%;
    object-fit:cover; 
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
    flex-direction: column;
    background-image: url(dsc.jpg);
    background-size:cover;
    background-position: center;
    
}
