body{
    background-color: #292c2f; 
}
.leftpart{
    width: 50%;
    height: 100vh;
    display: inline-block;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    background-color: white;

    h3{
        margin: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        
        a{
            text-decoration: none;
        }
    
    }
}
.rightpart{
    
    
    height: 100vh;
    width: 50%;
    display: inline-block;
    background-color: white;
    align-items: center;
    justify-content: center;
    
    
    position: relative;
    .images{
        height: 100%;
        width: 100%;
        
        background-color: #4dc7ff ;
    }
    
    .sun{
        position: absolute;
        width: 15%;
        bottom: 50%;
        left: 24%;
    }
    .colosseo{
        position: absolute;
        width: 60%;
        left: 14%;
        bottom: 0;
    }
    
}

@media all and (max-width:800px) //800px for tablets and phones.
{
    .rightpart, .leftpart
    {
        display: block; 
        float: none; 
        height: 50vh;
        width: 100%;
    }
}




